%chart-1 {
  width: 70px;
  height: 70px;
  margin-left: 15px;
  margin-top: 15px;
  line-height: 70px;
}
%chart-2 {
  width: 35px;
  height: 35px;
  margin-left: 7.5px;
  margin-top: 7.5px;
  line-height: 35px;
}
%chart-3 {
  width: 20px;
  height: 20px;
  margin-left: 5.5px;
  margin-top: 4.5px;
  line-height: 21px;
}


.chart-block {
  canvas {
    width: 100% !important;
  }
  .chart-overflow {
    overflow: hidden;
    text-align: center;
  }
}
.candidcahrt {
  width: 100%;
  height: 470px;
}
.amcharts-export-canvas {
  position: absolute;
  display: none;
  z-index: 1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: $white;
  .canvas-container {
    transition: opacity .5s ease-out;
  }
  &.active {
    display: block;
  }
  &.dropbox .canvas-container {
    opacity: .5;
  }
}
.flot-chart-container {
  //Instead of the line below you could use @include box-sizing($bs)
  box-sizing: border-box;
  width: 100%;
  height: 400px;
  #toggling-series-flot {
    width: 79% !important;
  }
}
.all-chart{
  .flot-chart-container{
    height: 350px;
  }
}
.flot-chart-placeholder {
  width: 100%;
  height: 100%;
  font-size: 14px;
  line-height: 1.2em;
  text-align: center;
  .legend table {
    border-spacing: 5px;
  }
}
.morris-hover {
  position: absolute;
  z-index: 8;
  &.morris-default-style {
    border-radius: 10px;
    padding: 6px;
    color: $dark-gray;
    background: $white;
    border: solid 1px $light-gray;
    font-family: $font-serif;
    font-size: 12px;
    text-align: center;
    .morris-hover-row-label {
      font-weight: bold;
      margin: 0.25em 0;
    }
    .morris-hover-point {
      white-space: nowrap;
      margin: 0.1em 0;
    }
  }
}
.chart-sparkline {
  position: absolute;
  left: 0;
  top: 0;
  visibility: hidden;
  background: rgba(49, 49, 49, 0.70);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#99000000, endColorstr=#99000000);
  color: $white;
  font: 10px $font-nunito, $font-serif;
  text-align: left;
  white-space: nowrap;
  padding: 5px;
  border: 1px solid rgba(49, 49, 49, 0.75);;
  box-sizing: content-box;
  z-index: 10000;
  border-radius: 5px;
  .jqsfield {
    color: $white;
    font: 10px $font-nunito, $font-serif;
    text-align: left;
  }
}
.radial-bar {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  background-color: transparent;
  box-sizing: content-box;
  width: 80px;
  height: 80px;
  font-size: 18px;
  background-clip: content-box;
  &:after {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    content: attr(data-label);
    background-color: $white;
    z-index: 1;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }
  > img {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 50%;
    text-align: center;
    font-weight: 500;
    color: $color-fiord;
    z-index: 3;
    width: 56px;
    height: 56px;
    margin-left: 12px;
    margin-top: 12px;
    line-height: 56px;
  }
}
.radial-bar-danger {
  &.radial-bar-20 {
    background-image: linear-gradient(90deg, $light-semi-gray 50%, transparent 50%, transparent), linear-gradient(162deg, $danger-color 50%, $light-semi-gray 50%, $light-semi-gray);
  }
}
.radial-bar{
  &.radial-bar-lg {
    width: 100px;
    height: 100px;
    font-size: 20px;
    &:after {
      @extend %chart-1;
    }
    > img {
      @extend %chart-1;
    }
  }
  &.radial-bar-sm {
    width: 50px;
    height: 50px;
    font-size: 12px;
    &:after {
      @extend %chart-2;
    }
    > img {
      @extend %chart-2;
    }
  }
  &.radial-bar-xs {
    width: 30px;
    height: 30px;
    font-size: 8px;
    &:after {
      @extend %chart-3;
    }
    > img {
      @extend %chart-3;
    }
  }
}
.pieLabel{
  div {
    color: $white !important;
    input[type=checkbox] {
      margin: 7px;
    }
  }
}
// knob chart css
.knob-chart{
  min-height: 255px;
  .chart-clock-main{
    position:relative;
    width:200px;
    margin:45px auto;
    .clock-large{
      position:absolute;
      left:-48px;
      top:-45px;
    }
    .clock-medium{
      position:absolute;
      left:2px;
      top:5px;
    }
    .clock-small{
      position:absolute;
      left:15px;
      top:55px;
    }
  }
}
// peity chart css
.peity-charts {
  svg {
    width: 100%;
    height: 250px;
  }
  &.peity-chart-primary{
    svg{
      polygon{
        fill: rgba($primary-color, 0.2);
      }
      polyline{
        stroke: $primary-color;
      }
    }
  }
}
.bar-odd-even {
  svg {
    rect {
      &:nth-child(odd) {
        fill: $primary-color;
      }
      &:nth-child(even) {
        fill: $secondary-color;
      }
    }
  }
}
.donut-peity {
  svg {
    path {
      &:nth-child(odd) {
        fill: $primary-color;
      }
      &:nth-child(even) {
        fill: $secondary-color;
      }
    }
  }
}
.small-donut, .donut-peity2, .pie-peity {
  ~ svg, svg {
    path {
      &:nth-child(odd) {
        fill: $primary-color;
      }
      &:nth-child(even) {
        fill: $light-semi-gray;
      }
    }
  }
}
.small-donut2 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $secondary-color;
      }
    }
  }
}
.small-donut3 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $success-color;
      }
    }
  }
}
.small-donut4 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $info-color;
      }
    }
  }
}
.small-donut5 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $warning-color;
      }
    }
  }
}
.small-donut6 {
  ~ svg {
    path {
      &:nth-child(odd) {
        fill: $danger-color;
      }
    }
  }
}
p{
  &.data-attributes{
    svg{
      margin-right: 10px;
    }
  }
}
// knob chart
.knob-block {
  position: relative;
  input{
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
    width: 100% !important;
    left: 0 !important;
    top: 0 !important;
    margin-left: 0 !important;
    margin-top: -3px !important;
  }
}
// apex chart
.apex-chart {
  margin: 0 auto;
}
.apexcharts-menu-item {
  width: 110px;
}
.word-tree{
  height: 400px;
}
// chartist chat 
.ct-series-a {
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $primary-color;
  }
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $primary-color;
  }
}
.ct-series-b {
  .ct-point, .ct-line, .ct-bar, .ct-slice-donut {
    stroke: $secondary-color;
  }
  .ct-slice-pie, .ct-slice-donut-solid, .ct-area {
    fill: $secondary-color;
  }
}




// echart css


.apache-cotainer {
  width: 100%;
  height: 350px;
  margin: 0;
}

.boxplot-cotainer {
  height: 420px;
}

.apache-cotainer-large {
  width: 100%;
  height: 500px;
  margin: 0;
}

.test-buttons {
  button {
    +button {
      margin-left: 10px;
    }
  }
}

.clip-chart-wrapper {
  .test-chart-block {
    margin-bottom: -30px;
  }
}

#echart-gauge {
  .test-chart {
    width: 100%;
    height: 395px;
  }
}

.polar-line {
  height: 350px;
}

#dotted {
  height: 390px;
}
