            }
            input{
                margin-right: 15px;
            }
            .input-group-prepend{
                .input-group-text{
                    input{
                        margin-right: 0;
                    }
                }
            }
            .input-group-btn{
                button{
                    border-radius: 4px;
                }
            }
        }
        select{
            border-radius: 4px;
        }
        .m-t-help-block{
            margin-top: -3px;
        }
    }
}
.form-builder-2-header{
    background-color: $light-color;
    padding: 15px;
    border-radius: 5px;
    display: flex;
    margin-bottom: 30px;
    > div{
        display: inline-block;
        width: 49%;
        nav{
            text-align: right;
            float: right;
        }
    }
}
.draggable{
    input{
        border-radius: 5px;
    }
    select{
        border-radius: 4px;
    }
}
/**=====================
     12. Form Builder 2 CSS Ends
==========================**/